@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* --------------------------------Navbar-Start--------------------------------------- */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
  width: 100%;
  z-index: 100;
  position: fixed;
}
.float {
  position: absolute;
}
.static-navbar {
  position: relative;
  width: 100%;
  padding-bottom: 60px;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "robot", sans-serif;
  letter-spacing: 2px;
}

.dropdown-menu {
  --bs-dropdown-min-width: 0;
  --bs-dropdown-zindex: 1000;
}
/* --------------------------------Navbar-End--------------------------------------- */

/* --------------------------------Footer-Start--------------------------------------- */
.footer {
  color: white;
  padding: 25px;
  background: -moz-linear-gradient(to right, #434343, #000000);
  /* chrome 10-25, safari 5.16 */
  background: linear-gradient(to right, #434343, #000000);
  margin-top: auto;
  width: 100%;
  position: relative;
  bottom: 0;
  /* padding-top: 60px;
  padding-bottom: 55px; */
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}
/* --------------------------------Footer-End--------------------------------------- */

/* --------------------------------Page-Not-Found-End--------------------------------------- */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: normal;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
/* --------------------------------Page-Not-Found-End--------------------------------------- */

/* --------------------------------Contact-Page-Start--------------------------------------- */
.contact-background {
  height: 100%;
  width: 100%;
  background-color: rgb(236, 182, 111);
}

.wrapper {
  background-image: url(pages/images/d.jpg);
  height: 692px;
  border: none;
  background-size: contain;
  width: auto;
  margin: auto auto auto 16%;
  background-repeat: no-repeat;
  /* filter: blur(2px); */
}

.inner {
  position: absolute;
  top: 20%;
  left: 48%;
  height: 460px;
  width: 500px;
  margin: auto;
  font-weight: bold;
  background: transparent;
  border-radius: 20px;
  border: none;
  padding: 30px 99px;
  backdrop-filter: blur(18px);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.contact-text {
  width: 300px;
  position: absolute;
  top: 68%;
  left: 25%;
}
.contact-text p {
  text-align: left;
}

.inner h3 {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 45px;
  font-family: "Montserrat-Bold";
  text-align: center;
  margin-bottom: 30px;
  color: black;
  margin-top: 0px;
}
.form-group {
  position: relative;
  display: block;
  margin-bottom: 30px;
  background: transparent;
}

.form-group span {
  background: none;
  font-size: 15px;
  color: black;
  position: absolute;
  top: 11px;
  transition: all 0.2s ease;
  transform-origin: 0 0;
  cursor: text;
}

.form-group span.border {
  height: 2px;
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  top: 41px;
  transform: scaleX(0);
  transition: all 0.15s ease;
}
.inner .form-control {
  color: black;
  border: none;
  border-bottom: 2px solid black;
  display: block;
  width: 100%;
  height: 43px;
  font-size: 18px;
  background: transparent;
  font-family: "Montserrat-SemiBold";
  padding-top: 15px;
  padding-bottom: 0%;
}

.form-control:focus {
  color: black;
  background: transparent;
  border-bottom: 2px solid black;
  box-shadow: none;
}

.form-control:focus + span,
.form-control:valid + span {
  transform: translateY(-22px) scale(1);
}

textarea.form-control {
  resize: none;
  padding-top: 20px;
  padding-bottom: 0px;
}
.inner button {
  border-radius: 15px;
  width: 162px;
  height: 51px;
  border: none;
  margin: auto;
  margin-top: 60px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  background-color: rgb(64, 71, 71);
  color: white;
  text-transform: uppercase;
  font-family: "Montserrat-SemiBold";
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.inner button i {
  margin-left: 10px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-transition-duration: 0.1s;
  transition-duration: 0.1s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.inner button:before {
  border-radius: 15px;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(81, 93, 93);
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.inner button:hover:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

@media (max-width: 767px) {
  h3 {
    font-size: 38px;
  }

  p {
    font-size: 14px;
    padding: 0;
  }

  .inner {
    padding: 27px 20px 37px;
    border: none;
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    -ms-box-shadow: none;
    -o-box-shadow: none;
  }
}
