.cart-page h1 {
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072) !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}

.cart-page .card {
  padding: 5px !important;
  height: 200px !important;
  margin-bottom: 5px !important;
}

.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-page .cart-summary {
  text-align: center;
  position: -webkit-sticky; /* Safari & IE */
  position: sticky;
  top: 30%;
  padding: 0px;
  margin: 0 0 0px 0;
}
.sticky-cart-summary {
  position: relative;
  padding: 0px 48px 20px 48px;
}

.cart-head {
  --bs-gutter-x: 0;
}
