.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 0px;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}
.home-page .card-name-price,
.search-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price,
.search-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}

/* ==================Filters============== */
.filter-sticky {
  position: relative;
  padding-bottom: 20px;
}
.filters {
  position: -webkit-sticky; /* Safari & IE */
  position: sticky;
  top: 20%;
}

.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper {
  margin-bottom: 5px !important;
  padding-left: 10px;
}

.ant-radio-wrapper {
  margin-bottom: 5px !important;
  padding-left: 10px;
}

.filters button {
  background-color: rgb(33, 31, 31);
  width: 100%;
  border: none;
  border-radius: 15px;
  margin-top: 20px;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
}
